.waveContainer {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;

	/* height: 100%; */
	/* margin-top: 10px; */
	/* Ensure it spans the parent */
}

.wave {
	position: absolute;
	bottom: 0;
	width: 100%;
	z-index: 10;
	transform: translateY(-50px);
}
